@import url('https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* font-family: 'Open Sans', sans-serif;
font-family: 'Poppins', sans-serif; */

/* //navbar css */
.navbar {

  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px rgb(245, 7, 7);
  --webkit-box-shadow: 0 8px 6px -6px rgb(233, 14, 14);
  border-bottom: solid rgb(233, 10, 10) !important;
}

.nav-link {
  font-weight: 300 !important;
}
.active {
  border-bottom: 2px solid rgb(232, 14, 14);
}

.navbar-brand {
  font-weight: 700;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 3px;
}

/* ==========footer=============  */

.footer {
 
  color: white;
  padding: 25px;
 
  background: linear-gradient(to right, #575151, #840f0f);
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #eb0d0d;
}
.text-center{
  font-size: medium;
}
/* =================================
======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}

/* ========================================= */
/* =========contact us ========= */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.heading{
  background: linear-gradient(to right, #575151, #840f0f);
}

.product-link{
  text-decoration: none !important;
  color: black !important;
  
}
.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #ce0707;
  color: white;
}
/* ================
====dashboard
=================== */

.dashboard {
  margin-top: 100px !important;
}

.dashboard-menu h4 {
  background-color: #434343 !important;
  color: white;
  padding: 20px 0px;
}